/**
 * Retrieves a color value from the $material-colors Sass map
 * Inspired by https://github.com/nilskaspersson/Google-Material-UI-Color-Palette/
 *
 * @function
 * @param {string} color-name             - quoted, lower-case, dasherized color
 *                                          name (e.g. 'pink', 'amber')
 * @param {string} [color-variant='500']  - quoted, lowercase color variant (e.g.
 *                                          '200', 'a100')
 */
html {
  box-sizing: border-box; }

body {
  margin: 0; }

*,
*:before,
*:after {
  box-sizing: inherit; }

.sol {
  min-width: 100vw;
  width: 100vw;
  max-width: 100vw;
  min-height: 100vh;
  height: 100vh;
  max-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: stretch; }
  .sol p,
  .sol a,
  .sol span {
    color: #2b2b2b;
    font-family: 'Zilla Slab', serif;
    font-size: 1.5em; }
  .sol h1,
  .sol h2,
  .sol h3,
  .sol h4,
  .sol h5,
  .sol h6 {
    margin: 0;
    color: #2b2b2b;
    font-family: 'Roboto', sans-serif;
    font-weight: 500;
    text-align: center;
    text-shadow: 1px 1px 1px #000; }
  .sol .content {
    min-width: 100vw;
    width: 100vw;
    max-width: 100vw;
    flex: 1;
    padding: 1rem 12vw;
    background: #e1f5fe; }
